import React, { Component } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Avatar from '@material-ui/core/Avatar';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
//
import bannerImg from 'images/bannerCropped.jpg';
import github from 'images/github.png';
import linkedin from 'images/linkedin.png';
import { researchTitle, experienceTitle, projectsTitle, goalsTitle, contactTitle, aboutmeTitle, aboutme } from 'strings.js';

const styles = {
	root: {
		overflow: 'hidden'
	},
	socialIcons: {
		position: 'absolute',
		marginTop: -130,
		right: 20
	},
	githubButton: {
		marginLeft: -12,
		marginRight: 40,
		width: 120,
		height: 120,
	},
	linkedinButton: {
		marginLeft: -10,
		marginRight: 20,
		width: 120,
		height: 120,
	},
	githubAvatar: {
		width: 80,
		height: 80,
	},
	linkedinAvatar: {
		width: 80,
		height: 80,
		padding: 14,
	},
	gridList: {
		width: '100%',
		height: '100%',
		// Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
		transform: 'translateZ(0)',
	},
	titleBar: {
	  background:
		'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
		'rgba(0,0,0,0.3) 90%, rgba(0,0,0,0) 100%)',
	},
};


class Body extends React.Component {
	render() {
		const sectionsData = [
			{
				name: researchTitle[this.props.language],
				img: ''
			},
			{
				name: experienceTitle[this.props.language],
				img: ''
			},
			{
				name: projectsTitle[this.props.language],
				img: ''
			},
			{
				name: goalsTitle[this.props.language],
				img: ''
			},
			{
				name: contactTitle[this.props.language],
				img: ''
			}
		];

		return (
			<div style={styles.root}>
				<img src={bannerImg} height="100%"/>
				<div style={styles.socialIcons}>
					<IconButton style={styles.githubButton} color="inherit" aria-label="github" href="https://github.com/bzylich">
						<Avatar src={github} style={styles.githubAvatar}/>
					</IconButton>
					<IconButton style={styles.linkedinButton} color="inherit" aria-label="linkedin" href="https://www.linkedin.com/in/brian-zylich/">
						<Avatar src={linkedin} style={styles.linkedinAvatar}/>
					</IconButton>
				</div>
				<div>
					<h2>{aboutmeTitle[this.props.language]}</h2>
					<p style={{textAlign: 'left', paddingLeft: 100, paddingRight: 100, paddingTop: 0, paddingBottom: 30}}>{aboutme[this.props.language]}</p>
				</div>
				<GridList cellHeight={200} spacing={4} style={styles.gridList}>
					{sectionsData.map(tile => (
						<GridListTile key={tile.img} cols={1} rows={1}>
							<img src={tile.img}/>
							<GridListTileBar
								title={tile.name}
								titlePosition="top"
								style={styles.titleBar}
							/>
						</GridListTile>
					))}
				</GridList>
			</div>
		);
	}
}

export default Body;


