import React, { Component } from 'react';
import ChatBot from 'react-simple-chatbot';
import './chatbot.css';

const styles = {
	root: {
		position: 'fixed',
		bottom: 0,
	},
	chatbot: {
		position: 'fixed',
		right: 0,
		bottom: 0,
	},
	mainBackground: {
		backgroundColor: 'rgba(0, 0, 0, 0.61)',
	}
};

class Footer extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			opened: true,
		};

		this.toggleChatbot = this.toggleChatbot.bind(this);
	}

	toggleChatbot({opened}) {
		this.setState({opened: opened});
	}

	render() {
		const steps = [
			{
				id: '0',
				message: 'Welcome to Brian\'s website!',
				trigger: '1',
			},
			{
				id: '1',
				user: true,
				trigger: ({value, steps}) => {
					if(value === 'bye') {
						return '3';
					}
					else {
						return '2';
					}
				},
			},
			{
				id: '2',
				message: ({ previousValue, steps }) => "You said: " + previousValue,
				trigger: '1',
			},
			{
				id: '3',
				message: 'Bye!',
				end: true,
			},
		];

		return (
			<div style={styles.root}>
				<ChatBot 
					style={styles.chatbot}
					bubbleStyle={styles.mainBackground}
					steps={steps} 
					headerTitle="Brian's Agent"
					userDelay={10}
					floating={true}
					opened={this.state.opened}
					toggleFloating={this.toggleChatbot}
				/>
			</div>
		);
	}
}

export default Footer;


